.section {
  margin: 0 0 2.5rem 0;

  > header {
    overflow: hidden;

    .title {
      padding: 0 0 0.5rem 0;
      transition: transform 100ms ease-in-out;
      transform-origin: top left;
      position: relative;

      &:before {
        content: " ";
        z-index: -1;
        background-color: rgba(255, 255, 255, 0.95);
        position: absolute;
        top: 0;
        right: -100vw;
        bottom: 0;
        left: 0;
      }
    }

    &.stuck {
      .title {
        transform: scale(0.85);

        &:before {
          border-bottom: 1px solid #ccc;
        }
      }
    }
  }

  > article {
    margin-bottom: 1.75rem;
    break-before: avoid;

    header {
      h1, h2, h3 {
        font-weight: lighter;

        strong {
          font-weight: 300;
        }
      }

      h1 {
        font-size: 1.75rem;
      }

      h2 {
        font-size: 1.1rem;
        margin-bottom: 1rem;
      }
    }
  }

  > h1 {
    margin-bottom: 1rem;
  }
}

.section-inline {
  margin: 0 0 1rem 0;

  > *:first-of-type {
    display: inline;
  }
}

.section-main {
  margin-bottom: 3rem;
}

