header.main {
  margin: 1.5rem 0 0.5rem 0;
  padding: 0.5rem 0 1rem 0;
  display: flex;
  align-items: baseline;
  position: sticky;
  top: 0;
  background-color: rgba(255, 255, 255, 0.95);
  z-index: 1;

  &.js-is-sticky {
    h1 {
      transform: scale(0.75);
    }
  }

  h1 {
    transform-origin: bottom left;
    transition: transform 100ms ease-in-out;
    font-weight: lighter;
    line-height: 1;
    font-size: 2.5rem;
    margin: 0 2rem 0 0;
    padding-left: 0;
    display: flex;
    align-items: baseline;

    @media (min-height: 560px) and (min-width: 768px) {
      font-size: 4rem;
    }

    &::before {
      content: " ";
      background-image: url(./i/mjd.png);
      background-size: cover;
      background-color: #fff;
      background-repeat: no-repeat;
      background-position: 50%;
      border-radius: 500px;
      border: 1px solid #ccc;
      overflow: hidden;
      width: 1em;
      height: 1em;
      align-self: center;
      flex-shrink: 0;
      flex-grow: 0;
      margin-right: 0.5rem;
      opacity: 0.8;
    }
  }

  ul.links {
    margin: 0.5rem 0 0 0;

    > li {
      margin: 0 1.5rem 0 0;
    }
  }
}

.header {
  font-weight: lighter;

  em {
    font-size: 0.65em;
    font-style: normal;
    color: #999;
  }
}

